const ajax = new XMLHttpRequest();
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';
const container = document.getElementById('root');

const store = {
    currentPage : 1
};

const getData = (url) => {
    ajax.open('GET', url, false);
    ajax.send();

    return JSON.parse(ajax.response);
}

const displayNewsFeed = () => {
    const newsFeeds = getData(NEWS_URL);

    //ì´ì™€ ê°™ì´ ë°˜ë³µë¬¸ì„ ì‚¬ìš©í•´ì„œ HTMLì„ ë¬¸ìì—´ì„ ì‚¬ìš©í•´ì„œ êµ¬í˜„í• ë•ŒëŠ” ë°°ì—´ì„ í™œìš©í•˜ëŠ”ê²Œ ê¸°ë³¸ì ì´ë‹¤.
    const newsList = ["<ul>"];
    const maxPageNumber = newsFeeds.length / 10;
    for (let i = (store.currentPage - 1) * 10 ; i < store.currentPage * 10 ; i++) {
        newsList.push(`<li><a href = #/detail/${newsFeeds[i].id}>${newsFeeds[i].title} (${newsFeeds[i].comments_count})</a></li>`);
    }
    newsList.push("</ul>");

    newsList.push(`
        <div>
            <a href="#/page/${store.currentPage - 1 > 1 ? store.currentPage - 1 : 1}">ì´ì „ í˜ì´ì§€</a>
            <a href="#/page/${store.currentPage + 1 > maxPageNumber ? maxPageNumber : store.currentPage + 1 }">ë‹¤ìŒ í˜ì´ì§€</a>
        </div>
    `);

    container.innerHTML = newsList.join('');
}

const displayNewsDetail = () => {
    // ì•µì»¤íƒœê·¸ì˜ í•´ì‹œê°€ ë³€ê²½ë˜ì—ˆì„ë•Œ ì´ë²¤íŠ¸ê°€ ë°œìƒí•œë‹¤.
    // í•´ì‹œë¥¼ CONTENT_URLì˜ idë€ì— ë„£ê³  APIë¥¼ í˜¸ì¶œí•´ì•¼í•¨
    // í•´ì‹œë¥¼ ì£¼ì†Œì—ì„œ ê°€ì ¸ì™€ì•¼ í•˜ëŠ”ë° ì£¼ì†Œ ë§¨ëì— í•´ì‹œê°€ ë¶™ì–´ìˆìœ¼ë‹ˆê¹Œ ì½”ë“œëŠ” ë‹¤ìŒê³¼ ê°™ë‹¤
    const id = location.hash.substring(9);

    const newsContent = getData(CONTENT_URL.replace('@id', id));

    container.innerHTML = `
        <h1>${newsContent.title}</h1>
        <div>
            <a href = "#/page/${store.currentPage}">ëª©ë¡ìœ¼ë¡œ</a>
        </div>
    `;
}

const router = () => {
    const routePath = location.hash;
    if (routePath == '') {
        // #ë§Œ ìˆìœ¼ë©´ ê·¸ëƒ¥ ë¹ˆ ë¬¸ìì—´ ''ë¡œ ë‚˜ì˜¨ë‹¤.
        displayNewsFeed();
    } else if (routePath.indexOf('/page') >= 0) {
        store.currentPage = Number(routePath.substring(7));
        displayNewsFeed();
    } else {
        displayNewsDetail();
    }
}

window.addEventListener('hashchange', router);

router();

